<template>
  <v-container >
    <v-row class="text-center" dense>
      <v-col cols="12" v-for="item in input_parameters" :key="item.name">
          <v-text-field
            :label="item.title"
            :value="item.value"
            v-model.number="item.value"
            outlined
            dense>
          </v-text-field>
      </v-col>
    </v-row>
    <v-row class="text-center" dense>
      Calculate Count : {{ calculate_count }}
    </v-row>
  </v-container>
</template>

<script>
import backend from '../backend';

  export default {
    data: () => ({
      name: 'vanillaoption',
      calculate_count: undefined,
      input_parameters:
      [
        {
          'title': 'S',
          'name': 'spot',
          'value': 100,
        },
        {
          'title': 'RiskFreeRate',
          'name': 'rf',
          'value': 0.03,
        },
        {
          'title': 'Dividend',
          'name': 'div',
          'value': 0.01,
        },
        {
          'title': 'Sigma',
          'name': 'vol',
          'value': 0.3,
        },
        {
          'title': 'Maturity',
          'name': 'maturity_t',
          'value': 1.0,
        },
      ],

    }),
    methods:{
      get_inputs(){
        var inputs = {'typename': 'vanillaoption', 'calctype': 'sync'};

        for (let i = 0; i < this.input_parameters.length; i++) {
          var item = this.input_parameters[i];
          inputs[item['name']] = item['value'];
        }

        return inputs;
      },
      increase_calc_count(){
        console.log(this.name)
        backend.event_count(this.name, 'add').then(response =>
        {
          // console.log(response.data)
          this.calculate_count = response.data['count'];
        });
      }

    },
    created() {
      backend.event_count(this.name, 'get').then(response =>
        {
          // console.log(response.data)
          this.calculate_count = response.data['count'];
        });
    },
  }
</script>
